@media only screen and (min-width: 320px) {
  .remodal-common {
    max-width: 500px !important;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-common {
    padding: 40px 20px;
    max-width: 450px !important;
    text-align: center;
  }
}
.remodal-common .remodal-close {
  color: #000 !important;
}
.error-input {
  clear: both;
  float: left;
  font-size: 13px;
  padding-left: 93px;
  color: #df3a3a;
  margin: 0 auto;
  text-align: center;
}
#name-error {
  font-size: 0 !important;
}
.input {
  position: relative;
}
label {
  position: absolute;
  top: 0;
  border: 0;
  font-size: 0 !important;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
.formBox {
  position: relative;
  width: 311px;
  margin: auto;
  padding-top: 7px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}
.formBox:before {
  content: '';
  position: absolute;
  width: 311px;
  height: 358px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -44%);
  background: url(../images/forms-sm.png) no-repeat center / contain;
}
.formBox form {
  position: relative;
  z-index: 10;
  max-width: 290px;
}
.formBox__title {
  font-size: 25px;
  font-weight: 700;
  color: #fff;
}
.formBox__text {
  display: block;
  padding: 10px 15px;
  font-size: 16px;
  color: #fff;
}
.formBox .form__line {
  display: block;
  padding: 8px 25px;
  margin: 15px auto;
  background: transparent;
  border: 1px solid #726f6c;
  width: 230px;
  border-radius: 20px;
}
.formBox .form__line:hover,
.formBox .form__line:focus {
  border: 1px solid #726f6c!important;
}
.formBox .btn {
  width: 230px;
}
.thanks__title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}
.thanks__text {
  display: block;
  padding-bottom: 15px;
  padding-left: 0;
  font-size: 18px;
  color: #000;
}
@keyframes arrow-bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-7px);
  }
}
@keyframes smoothScroll {
  0% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(0px);
  }
}
.slick-prev,
.slick-next {
  font-family: 'Fontello';
  transition: all .3s linear;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.slick-prev {
  left: -50px;
}
.slick-next {
  right: 50px;
}
.slick-prev:before,
.slick-next:before {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-25%);
  z-index: 10;
  color: #fff;
  font-size: 120px;
}
.slick-prev:before {
  left: 0;
}
.slick-next:before {
  right: 0;
}
.slick-dots {
  left: 50%;
  bottom: -40px;
  transform: translateX(-50%);
  border-image: transparent;
}
.slick-dots li {
  margin: 0 5px;
  width: 20px;
  height: 20px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 20px;
  width: 20px;
  font-size: 0px;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
